import { EditOutlined, FilterOutlined, PlusCircleOutlined, SearchOutlined, SyncOutlined } from "@ant-design/icons";
import { Button, Col, PageHeader, Radio, Row, Tooltip } from "antd";
import SearchFilterDropdown from "components/shared-components/SearchFilter";
import TableComponent from "components/shared-components/TableComponent/Index";
import RestrictedContainer from "components/util-components/RestrictedContainer";
import {
  ACTION,
  DATE_FORMAT,
  LIGHT_NAVY_BLUE_COLOR,
  NAVIGATION_ASSETS,
  NAVIGATION_ASSETS_RIGHTS,
  SORT_DIRECTIONS,
} from "constants/CommonConstants";
import { ACC_TABLE, ADD_ACCOUNT_TITLE, MANAGE_ACCOUNT_TITLE, STATUSES } from "constants/ManageAccounts";
import useAuthRights from "hooks/useAuthRights";
import useIntlMessage from "hooks/useIntlMessage";
import moment from "moment";
import { useEffect, useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getAllAccounts } from "redux/actions";
import { showLoader } from "redux/actions/Loader";
import Utils from "utils";
import ManageAccountsService from "../../../../services/ManageAccountService";
import AddAccountForm from "./AddAccountForm";

const ManageAccounts = () => {
  const userInfo = useSelector(state => state.myTeams.userInfo);
  let searchInput = useRef(null);
  const [searchText, setsearchText] = useState("");
  const [searchedColumn, setsearchedColumn] = useState("");
  const [data, setData] = useState([]);
  const [value, setValue] = useState(STATUSES.ACTIVE_STATUS.value);
  const [filtered, setFiltered] = useState([]);
  const [filteredInfo, setFilteredInfo] = useState({});
  const [show, setShow] = useState(false);
  const [editAccounts, setEditAccounts] = useState(null);
  const [sortedInfo, setSortedInfo] = useState({});
  const dispatch = useDispatch();
  const { hasAssetsRights } = useAuthRights();
  const getMessage = useIntlMessage();

  useEffect(() => {
    getManageAccounts();
  }, []);

  const getManageAccounts = async () => {
    try {
      dispatch(showLoader(true));
      const response = await ManageAccountsService.getAccountsListByEmpId();
      const filteredData = response.filter(
        el => el.status.toLowerCase() === STATUSES.ACTIVE_STATUS.value.toLowerCase(),
      );
      setData(response);
      setFiltered(filteredData);
    } catch (e) {
      console.error("Unable to populate project list", e);
    } finally {
      dispatch(showLoader(false));
      dispatch(getAllAccounts());
    }
  };

  const onChangeStatus = e => {
    setValue(e.target.value);
    if (e.target.value === STATUSES.ALL_STATUS.value) {
      setFiltered(data);
    } else {
      const filteredData = data.filter(el => el.status.toLowerCase() === e.target.value.toLowerCase());
      setFiltered(filteredData);
    }
  };

  const handleChange = (pagination, filters, sorter) => {
    setFilteredInfo(filters);
    setSortedInfo(sorter);
  };

  const clearFilters = () => {
    setFilteredInfo({});
  };
  const clearAll = () => {
    setFilteredInfo({});
    setSortedInfo({});
  };
  const AddAccount = () => {
    setShow(true);
  };
  const getColumnSearchProps = field => {
    let filterObj = {
      filterIcon: filtered => (
        <SearchOutlined
          id={`manage-account-search-btn-${field.key}`}
          color={filtered ? LIGHT_NAVY_BLUE_COLOR : undefined}
        />
      ),
    };
    if (field.type === "select") {
      if (!Utils.isEmpty(field.options)) {
        filterObj.filters = field.options.map(option => ({
          text: getMessage(option.label),
          value: option.value,
        }));
      }
      filterObj.filterIcon = filtered => (
        <FilterOutlined
          id={`manage-account-search-btn-${field.key}`}
          color={filtered ? LIGHT_NAVY_BLUE_COLOR : undefined}
        />
      );
      filterObj.onFilter = (value, record) => record[field.key] === value;
    } else {
      filterObj.filterDropdown = ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => (
        <SearchFilterDropdown
          setSelectedKeys={setSelectedKeys}
          selectedKeys={selectedKeys}
          confirm={confirm}
          clearFilters={clearFilters}
          handleSearch={handleSearch}
          dataIndex={field.key}
          searchInput={searchInput}
          handleReset={handleReset}
          label={getMessage(field.label)}
          inputId={`manage-account-input-${field.key}`}
          resetBtnId={`manage-account-reset-btn-${field.key}`}
          searchBtnId={`manage-account-search-btn-${field.key}`}
          dataSource={filtered}
        />
      );

      filterObj.onFilter = (value, record) => {
        if (field.type === "date") {
          const valuee = value.format(DATE_FORMAT).toString();
          const timeStampToDate = moment(record[field.key])?.format(DATE_FORMAT)?.toString();
          return timeStampToDate.toLowerCase().includes(valuee.toLowerCase());
        }
        return record[field.key]?.toString()?.toLowerCase().includes(value.toLowerCase());
      };
      filterObj.onFilterDropdownVisibleChange = visible => {
        if (visible) {
          setTimeout(() => searchInput && searchInput.select && searchInput.select());
        }
      };
    }
    return filterObj;
  };
  const handleSearch = (selectedKeys, confirm, dataIndex) => {
    confirm();
    setsearchText(selectedKeys[0]);
    setsearchedColumn(dataIndex);
  };
  const handleReset = (clearFilters, confirm) => {
    clearFilters();
    setsearchText("");
    confirm();
  };
  const handleRefresh = () => {
    getManageAccounts();
    setValue(STATUSES.ACTIVE_STATUS.value);
    clearAll();
    clearFilters();
    setsearchText("");
  };
  const EditAccount = data => {
    setEditAccounts(data);
    setShow(true);
  };

  const dataColumns = Object.values(ACC_TABLE).map(field => ({
    title: (
      <Tooltip title={getMessage(field.label)} placement="top">
        <span>{getMessage(field.label)}</span>
      </Tooltip>
    ),
    dataIndex: field.key,
    key: field.key,
    sorter: (a, b) => Utils.antdTableSorter(a, b, field.key),
    sortDirections: SORT_DIRECTIONS,
    filteredValue: filteredInfo[field.key] || null,
    sortOrder: sortedInfo.columnKey === field.key ? sortedInfo.order : null,
    ...getColumnSearchProps(field),
    render: (_, record) => {
      if (field.type === "date") {
        return moment(record[field.key]).format(DATE_FORMAT);
      }
      if (field.type === "select") {
        const selectedValueText = field.options.find(x => x.value === record[field.key])?.label;
        return selectedValueText ? getMessage(selectedValueText) : "";
      }
      return record[field.key];
    },
  }));

  if (hasAssetsRights(NAVIGATION_ASSETS.ManageAccount, NAVIGATION_ASSETS_RIGHTS.UPDATE)) {
    dataColumns.push({
      title: ACTION.text,
      key: ACTION.value,
      width: 120,
      render: record => (
        <Tooltip title={getMessage("common.label.edit")}>
          <Button
            id="manage-account-edit"
            onClick={() => {
              EditAccount(record);
            }}
            size="small"
          >
            <EditOutlined />
          </Button>
        </Tooltip>
      ),
    });
  }

  return (
    <>
      <Row align="middle" gutter={[20, 20]} className="mb-2">
        <Col xs={24} md={12} lg={12} xl={6}>
          <PageHeader
            className="p-2 pl-2"
            title={MANAGE_ACCOUNT_TITLE.text}
            subTitle={
              <Button className="ml-4" onClick={handleRefresh} id={"manage-account-refresh-btn"}>
                <SyncOutlined shape="circle" />
              </Button>
            }
          />
        </Col>

        <Col xs={24} md={12} lg={12} xl={14} className="pr-0 text-left text-md-right text-lg-left text-lg-right">
          {userInfo?.role === "Admin" || userInfo?.role === "Super_Admin" || userInfo?.role === "RMG" ? (
            <Radio.Group onChange={onChangeStatus} value={value} id="manage-account-status-group">
              {Object.values(STATUSES).map(status => (
                <Radio value={status.value} id={`manage-account-radio-btn-${status.value?.replace(/\s+/g, "")}`}>
                  {getMessage(status.label)}
                </Radio>
              ))}
            </Radio.Group>
          ) : null}
        </Col>

        <RestrictedContainer assetId={NAVIGATION_ASSETS.ManageAccount} assetRight={NAVIGATION_ASSETS_RIGHTS.CREATE}>
          <Col className="text-xl-right" xs={24} md={12} lg={24} xl={4}>
            <Button type="primary" onClick={AddAccount} id="manage-account-addbtn">
              <PlusCircleOutlined className="mr-2" />
              {ADD_ACCOUNT_TITLE.text}
            </Button>
          </Col>
        </RestrictedContainer>
      </Row>
      <TableComponent
        rowKey={record => record.accountId}
        columns={dataColumns}
        data={filtered}
        scroll={{ x: true }}
        handleChange={handleChange}
        tableSize="small"
        showDropdown={false}
        showSorterTooltip={false}
      />

      {show && (
        <AddAccountForm
          setIsVisible={show}
          editAccounts={editAccounts}
          onCancel={() => {
            setShow(false);
            setEditAccounts(null);
          }}
          handleRefresh={() => {
            getManageAccounts();
            setValue(STATUSES.ACTIVE_STATUS.value);
          }}
        />
      )}
    </>
  );
};

export default ManageAccounts;
