import { CloseOutlined, DeleteOutlined, PlusCircleOutlined, SaveOutlined } from "@ant-design/icons";
import { Button, Col, Form, Grid, Input, message, Modal, Row, Select, Table } from "antd";
import Flex from "components/shared-components/Flex";
import {
  CHILDREN,
  CONFIRM_MESSAGE,
  FORM_LAYOUT,
  MODAL_ADD_BUTTON_TYPE,
  MODAL_BACK_BUTTON,
  SELECT_METHODS,
  SUBMIT_TEXT,
  VALIDATION_FAILED,
} from "constants/CommonConstants";
import useIntlMessage from "hooks/useIntlMessage";
import { isEmpty } from "lodash";
import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import commonService from "services/CommonService";
import Utils from "utils";
import { ACC_FORM_FIELDS, LABELS, MESSAGES, STATUSES } from "../../../../constants/ManageAccounts";
import ManageAccountsService from "../../../../services/ManageAccountService";
import { ACTIVE_STATUS } from "../../../../constants/CommonConstants";
const { useBreakpoint } = Grid;

const AddAccountForm = ({ setIsVisible, onCancel, editAccounts, handleRefresh }) => {
  const [loading, setloading] = useState(false);
  const { IndustryType } = useSelector(state => state.myTeams.masterData);
  const employeeData = useSelector(state => state.myTeams.employeeData);
  const allLocations = useSelector(state => state.myTeams.allLocations?.map(x => x.country));
  const [technicalfunctionalGroupList, setTechnicalFunctionalGroup] = useState([]);
  const [form] = Form.useForm();
  const { Option, OptGroup } = Select;
  const { confirm } = Modal;
  const [hasChanges, setHasChanges] = useState(false);
  const [data, setData] = useState([]);
  const [Region, setRegion] = useState(null);
  const [functionalGroup, setFunctionalGroup] = useState([]);
  const [ccls, setCcls] = useState(null);
  const [isCclFormVisible, setCclFormVisiblity] = useState(false);
  const isMobile = !Utils.getBreakPoint(useBreakpoint()).includes("md");
  const [errorMessage, setErrorMessage] = useState(null);
  const [CDH, setCDH] = useState(null);
  const [CCLList, setCCLList] = useState([]);
  const getMessage = useIntlMessage();

  const formItemLayout = {
    labelCol: { span: 10 },
    wrapperCol: { span: 14 },
  };
  useEffect(() => {
    getTechnicalFunctionalGroup();
    if (editAccounts) {
      if (editAccounts.clientCountryLeads !== null) {
        assignKey(editAccounts?.clientCountryLeads);
      }
    }
  }, []);
  useEffect(() => {
    setCCLList([]);
    let listOfCCl = CDH
      ? employeeData.filter(e => e.employeeId !== CDH)
      : employeeData.filter(e => e.employeeId !== editAccounts?.clientDeliveryHead?.employeeId);
    setCCLList(listOfCCl);
    if (ccls === CDH && !Utils.isEmpty(CDH)) {
      setErrorMessage(getMessage(MESSAGES.CCL_AND_CDH_DUPLICATE));
    } else {
      setErrorMessage(false);
    }
  }, [CDH, employeeData, editAccounts, ccls]);

  const disabledStatus = React.useCallback(() => {
    if (!editAccounts) {
      return true;
    }
  }, []);
  const assignKey = obj => {
    const editdata = obj?.map((x, id) => {
      return {
        key: id,
        region: x.region,
        functionalGroups: x.functionalGroups,
        employeeId: x.employeeId,
      };
    });
    setData(editdata);
  };
  // const mergeLocations = () => {
  //   const merged = Array.prototype.concat(...allLocations);
  //   setLocationsList(merged);

  // }
  const handleAdd = values => {
    const _value = data && data[data.length - 1]?.key;
    const tableData = {
      key: (_value || 0) + 1,
      region: Region,
      functionalGroups: functionalGroup,
      employeeId: ccls,
    };

    let dtableData = Utils.isEmpty(data) ? [tableData] : [...data, tableData];
    setData(dtableData);
    setCclFormVisiblity(false);
    setHasChanges(true);
    resetFields();
  };
  const resetFields = () => {
    form.setFieldsValue({ functionalGroups: [], region: null, clientCountryLeads: null });
    setFunctionalGroup([]);
    setRegion(null);
    setCcls(null);
  };
  const handleDelete = key => {
    setHasChanges(true);
    let dtableData = data.filter(x => x.key !== key);
    setData(dtableData);
  };
  const columns = [
    {
      title: getMessage(ACC_FORM_FIELDS.CCLS.REGION.label),
      dataIndex: ACC_FORM_FIELDS.CCLS.REGION.name,
      key: ACC_FORM_FIELDS.CCLS.REGION.name,
    },
    {
      title: getMessage(ACC_FORM_FIELDS.CCLS.FUNC_GROUP.label),
      dataIndex: ACC_FORM_FIELDS.CCLS.FUNC_GROUP.name,
      key: ACC_FORM_FIELDS.CCLS.FUNC_GROUP.name,
      render: services => services?.join(","),
    },
    {
      title: getMessage(ACC_FORM_FIELDS.CCLS.CCL_NAME.label),
      dataIndex: ACC_FORM_FIELDS.CCLS.CCL_NAME.name,
      key: ACC_FORM_FIELDS.CCLS.CCL_NAME.name,
      render: employeeId => employeeData.find(x => x.employeeId === employeeId)?.employeeName,
    },
    {
      title: getMessage(LABELS.ACTIONS),
      key: "actions",
      render: record => (
        <Button onClick={() => handleDelete(record.key)} id={"manage-account-delete-CCL"}>
          <DeleteOutlined />
        </Button>
      ),
    },
  ];
  const handleConfirm = () => {
    confirm({
      title: CONFIRM_MESSAGE.value,
      centered: true,
      onOk() {
        onCancel();
      },
    });
  };

  const confirmationCCls = () => {
    confirm({
      title: CONFIRM_MESSAGE.value,
      centered: true,
      onOk() {
        resetFields();
        setCclFormVisiblity(false);
      },
    });
  };
  const closeCCls = () => {
    if (form.isFieldsTouched()) {
      confirmationCCls();
    } else {
      setCclFormVisiblity(false);
    }
  };

  const onCreateAccount = async values => {
    const dataToPass = ManageAccountsService.prepareDataForAddAccount(values, data);
    const response = await ManageAccountsService.CreateAccount(dataToPass);
    if (response?.statusCode) {
      message.success(getMessage(MESSAGES.ADD_SUCCESS));
      handleRefresh();
      onCancel();
      form.resetFields();
    } else {
      // message.error(getMessage(MESSAGES.CCL_AND_CDH_DUPLICATE));
    }
  };
  const onUpdateAccount = async values => {
    const dataToPass = ManageAccountsService.prepareDataForUpdateAccount(editAccounts, values, data);
    const response = await ManageAccountsService.Update(editAccounts?.accountId, dataToPass);
    if (response?.statusCode) {
      message.success(getMessage(MESSAGES.UPDATE_SUCCESS));
      handleRefresh();
      onCancel();
      form.resetFields();
    } else {
      // message.error(getMessage(MESSAGES.CCL_AND_CDH_DUPLICATE));
    }
  };
  const handleOk = () => {
    form
      .validateFields()
      .then(values => {
        if (performValidations({ ...values, ccls: data })) {
          saveData(values);
        }
      })
      .catch(info => {
        console.log(VALIDATION_FAILED.value, info);
      });
  };
  const saveData = async values => {
    try {
      setloading(true);
      if (editAccounts) {
        await onUpdateAccount(values);
      } else {
        await onCreateAccount(values);
      }
    } catch (e) {
      console.log(e);
    } finally {
      setloading(false);
    }
  };
  const performValidations = formData => {
    const error = ManageAccountsService.performDataValidations(formData);
    if (isEmpty(error)) {
      return true;
    }
    setErrorMessage(getMessage(error));
    return false;
  };
  const handleCancel = () => {
    if (form.isFieldsTouched() || hasChanges) {
      handleConfirm();
    } else {
      onCancel();
    }
  };
  const handleCCLS = () => {
    if (
      Utils.isEmpty(ccls) ||
      Utils.isEmpty(Region) ||
      Utils.isEmpty(functionalGroup) ||
      (ccls === CDH && !Utils.isEmpty(CDH))
    ) {
      return null;
    } else {
      return (
        <Button onClick={handleAdd} className="mr-2" id={"manage-account-save-ccl"}>
          <SaveOutlined />
        </Button>
      );
    }
  };
  const getTechnicalFunctionalGroup = async () => {
    const technicalFunctionalGroup = await commonService.fetchAllFunctionalGroupsTechnical();
    if (technicalFunctionalGroup) {
      setTechnicalFunctionalGroup(technicalFunctionalGroup);
    }
  };
  const initials = {
    accountName: editAccounts?.accountName,
    industryType: editAccounts?.industryType,
    clientAddress: editAccounts?.clientAddress,
    clientPortfolioManager: editAccounts?.clientPortfolioManager?.employeeId || [],
    clientDeliveryHead: editAccounts?.clientDeliveryHead?.employeeId || [],
    status: editAccounts?.status,
    clientPartner: editAccounts?.clientPartner?.employeeId || [],
  };
  return (
    <Modal
      visible={setIsVisible}
      title={editAccounts ? getMessage(LABELS.EDIT_ACCOUNT_TITLE) : getMessage(LABELS.ADD_ACCOUNT_TITLE)}
      onCancel={handleCancel}
      onOk={handleOk}
      width={isMobile ? "80%" : "90%"}
      footer={[
        <Button key={MODAL_BACK_BUTTON.value} onClick={handleCancel} id={"manage-account-cancel-btn"}>
          {getMessage(LABELS.CANCEL_TEXT)}
        </Button>,
        <Button
          key={SUBMIT_TEXT.value}
          onClick={handleOk}
          type={MODAL_ADD_BUTTON_TYPE.value}
          loading={loading}
          id={"manage-account-ok-btn"}
        >
          {editAccounts ? getMessage(LABELS.UPDATE_TEXT) : getMessage(LABELS.ADD_TEXT)}
        </Button>,
      ]}
      okButtonProps={{ loading: loading, id: "add-account-addBtn" }}
      cancelButtonProps={{ id: "add-account-cancelBtn" }}
      closeIcon={<CloseOutlined id="add-account-close-modal-btn" />}
    >
      <Form
        {...formItemLayout}
        form={form}
        layout={FORM_LAYOUT.horizontal}
        name={ACC_FORM_FIELDS.FORM_NAME.name}
        initialValues={initials}
        labelAlign="left"
      >
        <Row gutter={24}>
          <Col xs={24} md={12} lg={8}>
            <Form.Item
              name={ACC_FORM_FIELDS.ACC_NAME.name}
              label={getMessage(ACC_FORM_FIELDS.ACC_NAME.label)}
              rules={[
                {
                  required: true,
                },
              ]}
            >
              <Input placeholder={getMessage(ACC_FORM_FIELDS.ACC_NAME.label)} id="manage-account-account-input" />
            </Form.Item>
          </Col>
          <Col xs={24} md={12} lg={8}>
            <Form.Item
              name={ACC_FORM_FIELDS.INDUSTRY_TYPE.name}
              label={getMessage(ACC_FORM_FIELDS.INDUSTRY_TYPE.label)}
              rules={[
                {
                  required: true,
                },
              ]}
            >
              <Select placeholder={getMessage(ACC_FORM_FIELDS.INDUSTRY_TYPE.label)} id="manage-account-industry">
                {IndustryType?.map(e => {
                  return (
                    <Option key={e} value={e}>
                      {e.charAt(0)?.toUpperCase() + e.slice(1)}
                    </Option>
                  );
                })}
              </Select>
            </Form.Item>
          </Col>
          <Col xs={24} md={12} lg={8}>
            <Form.Item
              name={ACC_FORM_FIELDS.CLIENT_ADDRESS.name}
              label={getMessage(ACC_FORM_FIELDS.CLIENT_ADDRESS.label)}
              rules={[
                {
                  required: true,
                },
              ]}
            >
              <Input placeholder={getMessage(ACC_FORM_FIELDS.CLIENT_ADDRESS.label)} id="manage-account-address-input" />
            </Form.Item>
          </Col>
          <Col xs={24} md={12} lg={8}>
            <Form.Item
              name={ACC_FORM_FIELDS.CDH.name}
              label={getMessage(ACC_FORM_FIELDS.CDH.label)}
              rules={[
                {
                  required: true,
                },
              ]}
            >
              <Select
                placeholder={getMessage(ACC_FORM_FIELDS.CDH.label)}
                showSearch
                optionFilterProp="children"
                id="manage-account-cdh-input"
                onSelect={e => setCDH(e)}
              >
                {employeeData?.map(e => {
                  return (
                    <Option key={e.employeeId} value={e.employeeId}>
                      {e.employeeName}
                    </Option>
                  );
                })}
              </Select>
            </Form.Item>
          </Col>
          <Col xs={24} md={12} lg={8}>
            <Form.Item
              name={ACC_FORM_FIELDS.CPM.name}
              label={getMessage(ACC_FORM_FIELDS.CPM.label)}
              rules={[
                {
                  required: true,
                },
              ]}
            >
              <Select
                placeholder={getMessage(ACC_FORM_FIELDS.CPM.label)}
                showSearch
                optionFilterProp={CHILDREN}
                id="manage-account-cpm-input"
              >
                {employeeData?.map(e => {
                  return (
                    <Option key={e.employeeId} value={e.employeeId}>
                      {e.employeeName}
                    </Option>
                  );
                })}
              </Select>
            </Form.Item>
          </Col>
          <Col xs={24} md={12} lg={8}>
            <Form.Item
              name={ACC_FORM_FIELDS.CP.name}
              label={getMessage(ACC_FORM_FIELDS.CP.label)}
              rules={[
                {
                  required: true,
                },
              ]}
            >
              <Select
                placeholder={getMessage(ACC_FORM_FIELDS.CP.label)}
                showSearch
                optionFilterProp={CHILDREN}
                id="manage-account-cpm-input"
              >
                {employeeData?.map(e => {
                  return (
                    <Option key={e.employeeId} value={e.employeeId}>
                      {e.employeeName}
                    </Option>
                  );
                })}
              </Select>
            </Form.Item>
          </Col>
          <Col xs={24} md={12} lg={8}>
            <Form.Item
              name={ACC_FORM_FIELDS.STATUS.name}
              label={getMessage(ACC_FORM_FIELDS.STATUS.label)}
              rules={[{ required: disabledStatus() ? false : true }]}
              initialValue={STATUSES.ACTIVE_STATUS.value}
            >
              {disabledStatus() ? (
                <span className="profile-text-color">{ACTIVE_STATUS.text}</span>
              ) : (
                <Select
                  // disabled={disabledStatus()}
                  placeholder={getMessage(ACC_FORM_FIELDS.STATUS.label)}
                  allowClear
                  id="manage-account-status-input"
                >
                  {Object.values(STATUSES)
                    .filter(x => x.value !== STATUSES.ALL_STATUS.value)
                    .map(e => {
                      return <Option value={e.value}>{getMessage(e.label)}</Option>;
                    })}
                </Select>
              )}
            </Form.Item>
          </Col>
        </Row>

        <Row className="mb-2">
          <Col xs={12} md={4} lg={4}>
            <span>
              <h3>{getMessage(ACC_FORM_FIELDS.CCLS.title)}</h3>
            </span>
          </Col>
          <Col xs={12} md={4} lg={4}>
            <Button
              onClick={() => {
                setCclFormVisiblity(true);
              }}
              id={"manage-account-show-ccl-btn"}
            >
              <PlusCircleOutlined />
            </Button>
          </Col>
          {errorMessage && (
            <Col xs={12} md={16}>
              <div style={{ color: "red" }}>* {errorMessage}</div>
            </Col>
          )}
        </Row>
        {isCclFormVisible && (
          <Row gutter={12}>
            <Col xs={24} md={12} lg={6}>
              <Form.Item
                name={ACC_FORM_FIELDS.CCLS.REGION.name}
                label={getMessage(ACC_FORM_FIELDS.CCLS.REGION.label)}
                required
                rules={[
                  {
                    required: true,
                  },
                ]}
              >
                <Select
                  placeholder={getMessage(ACC_FORM_FIELDS.CCLS.REGION.label)}
                  showSearch
                  optionFilterProp={CHILDREN}
                  onSelect={e => setRegion(e)}
                  id="manage-account-region-input"
                >
                  {Utils.sortArray(allLocations).map(x => (
                    <Option key={x} value={x}>
                      {x}
                    </Option>
                  ))}
                </Select>
              </Form.Item>
            </Col>
            <Col xs={24} md={12} lg={6}>
              <Form.Item
                name={ACC_FORM_FIELDS.CCLS.FUNC_GROUP.name}
                label={getMessage(ACC_FORM_FIELDS.CCLS.FUNC_GROUP.label)}
                required
                rules={[
                  {
                    required: true,
                  },
                ]}
              >
                <Select
                  id="manage-account-functionalgroup-input"
                  placeholder={getMessage(ACC_FORM_FIELDS.CCLS.FUNC_GROUP.label)}
                  showSearch
                  showArrow
                  optionFilterProp={CHILDREN}
                  onChange={e => setFunctionalGroup(e)}
                  mode={SELECT_METHODS.MULTIPLE}
                >
                  {technicalfunctionalGroupList?.map(x => (
                    <Option key={x} value={x}>
                      {x}
                    </Option>
                  ))}
                </Select>
              </Form.Item>
            </Col>
            <Col xs={24} md={12} lg={6}>
              <Form.Item
                name={ACC_FORM_FIELDS.CCLS.CCL.name}
                label={getMessage(ACC_FORM_FIELDS.CCLS.CCL.label)}
                required
                rules={[
                  {
                    required: true,
                  },
                ]}
              >
                <Select
                  placeholder={getMessage(ACC_FORM_FIELDS.CCLS.CCL.label)}
                  showSearch
                  optionFilterProp={CHILDREN}
                  onSelect={e => setCcls(e)}
                  id="manage-account-ccl-input"
                >
                  {CCLList?.map(e => {
                    return (
                      <Option key={e.employeeId} value={e.employeeId}>
                        {e.employeeName}
                      </Option>
                    );
                  })}
                </Select>
              </Form.Item>
            </Col>
            <Col xs={24} md={12} lg={6}>
              <Flex alignItems="center">
                <span className="mr-2">
                  <h5>{getMessage(LABELS.ACTIONS)}</h5>
                </span>
                <Row>
                  <Col>{handleCCLS()}</Col>
                  <Col>
                    <Button onClick={closeCCls} id={"manage-account-close-btn"}>
                      <CloseOutlined />
                    </Button>
                  </Col>
                </Row>
              </Flex>
            </Col>
          </Row>
        )}
      </Form>
      <Table dataSource={data} columns={columns} />
    </Modal>
  );
};

export default AddAccountForm;
